<template>
    <div id="single-blog">
        <h1>{{blog.title}}</h1>
        <article>
            {{blog.body}}
        </article>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    name: "single-blog",
    data() {
        return {
            id: this.$route.params.id,
            blog: {}
        }
    },
    created() {
        // this.$http.get('http://jsonplaceholder.typicode.com/posts/' + this.id)
        axios.get('http://jsonplaceholder.typicode.com/posts/' + this.id)
         .then((data) => {
             console.log(data.data);
            this.blog = data.data;
         })
    }
}
</script>

<style scoped>
#single-blog{
    max-width: 960px;
    margin: 0 auto;
    padding: 20px;
    background: #eee;
    border: 1px solid #aaa;
}



</style>





